










































































import { Component, Vue } from 'vue-property-decorator';
import TopBar from '@/components/TopBar.vue';
import FooterComponent from '@/components/FooterComponent.vue';
import FabButton from '@/components/FabButton.vue';
import ContactDialog from '@/components/ContactDialog.vue';
import Home from '@/views/Home.vue';
import WelcomeDialog from '@/components/WelcomeDialog.vue';
@Component({
  components: {
    WelcomeDialog,
    Home,
    FabButton,
    FooterComponent,
    'top-bar': TopBar,
    'contact-dialog': ContactDialog,
    'welcome-dialog': WelcomeDialog
  }
})
export default class App extends Vue {
  contactDialog = false;
  welcomeDialog = false;
  isMessage = false;
  isOralEval = false;
  successSnackbar = false;
  errorSnackbar = false;
  invalidSnackbar = false;

  created() {
    setTimeout(() => {
      this.popupWelcomeDialog();
    }, 1500);
  }

  popupWelcomeDialog() {
    this.welcomeDialog = true;
  }
  onCloseWelcomeDialog() {
    this.welcomeDialog = false;
  }

  onMessageUs() {
    this.isMessage = true;
    this.contactDialog = true;
    this.isOralEval = false;
  }
  onAppointment() {
    this.isMessage = false;
    this.contactDialog = true;
    this.isOralEval = false;
  }

  onAppointmentOralEval() {
    this.isMessage = false;
    this.contactDialog = true;
    this.isOralEval = true;
    this.welcomeDialog = false;
  }

  successNotification() {
    return 'A sua mensagem foi enviada e ser respondida nas prximas 48 horas. Obrigado pela preferncia.';
  }

  errorNotification() {
    return 'Mensagem no enviada. Tente novamente.';
  }

  emailInvalidNotification() {
    return 'Email no  vlido.';
  }

  onSuccess() {
    this.successSnackbar = true;
  }

  onError() {
    this.errorSnackbar = true;
  }

  onEmailInvalid() {
    this.invalidSnackbar = true;
  }
  onCloseDialog() {
    this.contactDialog = false;
  }
}
