


































































































































































































































import { Component, Model, Prop, Vue } from 'vue-property-decorator';
import PrivacyPolicyDialog from '@/components/PrivacyPolicyDialog.vue';
import emailJs from 'emailjs-com';
import { emailValidator } from '@/services/RemoteService';
import VueCtkDateTimePicker from 'vue-ctk-date-time-picker';
import 'vue-ctk-date-time-picker/dist/vue-ctk-date-time-picker.css';

@Component({
  components: {
    'privacy-policy-dialog': PrivacyPolicyDialog,
    'vue-ctk-date-time-picker': VueCtkDateTimePicker
  }
})
export default class ContactDialog extends Vue {
  @Model('dialog', Boolean) dialog!: boolean;
  @Prop(Boolean) isMessage!: boolean;
  @Prop(Boolean) isOralEval!: boolean;

  privacyDialog = false;
  isValid = true;
  name = '';
  phone = '';
  email = '';
  subject = '';
  messageText = this.isOralEval ? 'Avaliao mdica dentria' : '';
  agreement = false;
  isAgreementSeen = false;
  preferentialDateTime = '';
  alternativeDateTime = '';

  getDisabledHours() {
    return [
      '00',
      '01',
      '02',
      '03',
      '04',
      '05',
      '06',
      '07',
      '08',
      '18',
      '19',
      '20',
      '21',
      '22',
      '23'
    ];
  }

  appointmentDateTime() {
    return 'Data/hora preferencial';
  }

  appointmentDateTimeAlternative() {
    return 'Data/hora alternativa';
  }

  warningEmail() {
    if (this.$store.getters.isPortuguese) {
      return 'Email invlido';
    } else {
      return 'Invalid Email';
    }
  }

  warningPhone() {
    if (this.$store.getters.isPortuguese) {
      return 'Nmero de Telemvel invlido';
    } else {
      return 'Invalid phone number';
    }
  }

  fullName() {
    if (this.$store.getters.isPortuguese) {
      return 'Nome Completo';
    } else {
      return 'Full Name';
    }
  }

  phoneNumber() {
    if (this.$store.getters.isPortuguese) {
      return 'Telemvel';
    } else {
      return 'Mobile Phone';
    }
  }

  warning() {
    if (this.$store.getters.isPortuguese) {
      return 'Campo de preenchimento obrigatrio';
    } else {
      return 'Required field';
    }
  }

  warningDataInvalid() {
    return 'Data invlida';
  }

  warningText() {
    if (this.$store.getters.isPortuguese) {
      return 'Campo de preenchimento obrigatrio com limite de caractres';
    } else {
      return 'Required field with character limit';
    }
  }

  warningPrivacy() {
    if (this.$store.getters.isPortuguese) {
      return 'Tem que concordar para continuar';
    } else {
      return 'Agree to continue';
    }
  }

  textFiledLabel() {
    if (this.$store.getters.isPortuguese) {
      if (this.isMessage) {
        return 'Mensagem';
      } else {
        return 'Motivo da Consulta';
      }
    } else {
      if (this.isMessage) {
        return 'Message';
      } else {
        return 'Appointment reason';
      }
    }
  }

  dialogTitle() {
    if (this.$store.getters.isPortuguese) {
      if (!this.isMessage && !this.isOralEval) {
        return 'Agendamento de Consulta';
      } else if (this.isOralEval) {
        return 'Avaliao Mdica Dentria';
      } else {
        return 'Pedido de Contacto';
      }
    } else {
      if (this.$store.getters.isEnglish) {
        if (!this.isMessage) {
          return 'Ask for an Appointment';
        } else {
          return 'Message Us';
        }
      }
    }
  }

  closeTooltip() {
    if (this.$store.getters.isPortuguese) {
      return 'Fechar Formulrio';
    } else {
      return 'Close Form';
    }
  }

  closeDialog() {
    this.$emit('close-dialog');
  }

  privacyLabel1() {
    if (this.$store.getters.isPortuguese) {
      return 'Declaro que li e concordo com a';
    } else {
      return 'I acknowledge that I have read and agree with the';
    }
  }

  privacyLabel2() {
    if (this.$store.getters.isPortuguese) {
      return 'Poltica de Privacidade.';
    } else {
      return 'Privacy Policy.';
    }
  }

  openPrivacyPolicy() {
    this.agreementSeen();
    this.privacyDialog = true;
  }

  agreementSeen() {
    this.isAgreementSeen = true;
  }

  openFirstPrivacyPolicy() {
    if (!this.isAgreementSeen) this.privacyDialog = true;
    this.agreementSeen();
  }

  onClosePrivacyDialog() {
    this.privacyDialog = false;
  }

  cleanFormLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'Limpar dados';
    } else {
      return 'Clean Form';
    }
  }

  submitFormLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'Enviar';
    } else {
      return 'Submit';
    }
  }

  async onSubmit() {
    let template: string;
    let templateParams: {};
    if (this.isMessage) {
      template = 'contacto';
      templateParams = {
        fromName: this.name,
        fromEmail: this.email,
        fromPhone: this.phone,
        messageSubject: this.dialogTitle(),
        messageText: this.messageText
      };
    } else {
      template = 'agendamento';
      templateParams = {
        fromName: this.name,
        fromEmail: this.email,
        fromPhone: this.phone,
        prefDateTime: this.preferentialDateTime,
        altDateTime: this.alternativeDateTime,
        messageSubject: this.dialogTitle(),
        messageText: this.messageText
      };
    }

    const response = await emailValidator(this.email);

    console.log(response);

    if (response.status === 200) {
      response.data.deliverability === 'DELIVERABLE'
        ? this.sendEmail(templateParams, template)
        : this.$emit('invalid-snackbar');
    } else {
      this.sendEmail(templateParams, template);
    }
  }

  checkDateAppointment(date: string) {
    const year = +date.slice(0, 4);
    const month = +date.slice(5, 7) - 1;
    const day = +date.slice(8, 10);
    const dateConversion = new Date(Date.UTC(year, month, day, 0, 0, 0));
    return !(dateConversion.getDay() === 0);
  }

  getCurrentDate() {
    return new Date().toJSON();
  }

  sendEmail(templateParams: {}, template: string) {
    const EMAIL_JS_USER_ID = 'user_0P8qpeCBKoDuSLGXpwO3x';
    const EMAIL_JS_SERVICE_ID = 'cmdmiratejo';
    emailJs
      .send(EMAIL_JS_SERVICE_ID, template, templateParams, EMAIL_JS_USER_ID)

      .then(
        result => {
          console.log('SUCCESS!', result.status, result.text);
          this.$emit('success-snackbar');
          this.closeDialog();
        },
        error => {
          this.$emit('error-snackbar');
          console.log('FAILED', error);
        }
      );
  }
}
