














































































import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {}
})
export default class HomeComponent extends Vue {
  alertText() {
    return (
      'Sabia que a visita regular ao dentista  uma regra de ouro para manter uma boa sade oral?<p><p>' +
      '<strong>Marque j a sua avaliao mdica dentria sem qualquer compromisso!</strong>'
    );
  }
  appTitle() {
    return this.$store.getters.getAppTitle.toUpperCase();
  }

  getColor() {
    return this.$store.getters.getBaseColor;
  }

  slogan() {
    if (this.$store.getters.isPortuguese) {
      return 'A sua <strong>SADE ORAL</strong>  a nossa prioridade!';
    } else {
      return 'Your <strong>ORAL HEALTH</strong> is our priority!';
    }
  }
  alignment = 'end';
  position = 'center';
}
